//import PropTypes from "prop-types"
import React from "react"
import styled from "styled-components"
import cx from "classnames"
import { useStaticQuery, graphql } from "gatsby"

import useWindowScrollDirection from "~hooks/useWindowScrollDirection"
import NavbarLinks from "./NavbarLinks"
import Logo from "../Logo"

/*  Header  wrapper */
const HeaderWrapper = styled.header`
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 0.5rem;
  z-index: 5;
  border-bottom: 1px solid;
  box-shadow: 0 0 0.4rem 0 black;
  max-height: 80px;
  transition: top 0.6s ease;

  &.hide {
    top: -6rem;
  }
`

/* Header  part */
const MainHeader = styled.div`
  padding: 0.3rem;
  display: flex;
  justify-content: center;
`

/* Header  part */
const SubHeader = styled.div`
  padding: 0.2rem;
  display: flex;
  justify-content: center;
`

/*  Navigation   Section */
const NavSection = styled.div`
  display: flex;
  height: 100%;
  align-items: center;
`

const Header = () => {
  const data = useStaticQuery(graphql`
    query MenuLinkQuery {
      site {
        siteMetadata{
          menuLinks{
            name
            link
          }
          author{
            socials{
              name
              author
            }
          }
        }
      }
    }
  `)

  const { isUp } = useWindowScrollDirection()
  return(
    <HeaderWrapper className={cx({ 'hide' : !isUp })}>
      <MainHeader>
        <NavSection>
          <Logo/>
        </NavSection>
      </MainHeader>
      <SubHeader>
        <NavSection>
          <NavbarLinks data={data.site.siteMetadata.menuLinks}/>
        </NavSection>
      </SubHeader>
    </HeaderWrapper>
  )
}

export default Header;